.App {
  font-family: "Droid Serif";
}
body {
  font-family: "Droid Serif";
  box-sizing: border-box;
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(250, 250, 250, 1);
}

li {
  float: left;
}

li a {
  display: block;
  color: rgba(85, 83, 255, 1);
  text-align: center;
  padding: 18px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  /* background-color: #04aa6d; */
}

.active {
  background-color: #04aa6d;
}
